.movie-item {
  display: block;
  //width: 190px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  //transition-property: opacity, transform;
  //transition-duration: 0.3s;
  //transition-timing-function: ease;
  transform: scale(0, 0);
  opacity: 0;
  cursor: pointer;
  font-size: 85%;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    vertical-align: top;
    transition-property: all;
    transition-duration: 1s;
    transition-timing-function: linear;
  }

  &__details {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    //text-shadow: 2px 2px rgba(176, 172, 176, 0.09);
    opacity: 0;
    transition-property: all;
    transition-duration: 0.15s;
    transition-timing-function: linear;
    position: relative;
    width: 100%;
    height: 100%;
    color: white;
    //background: rgba(133, 235, 217, 0.9);
    padding: 4px;
    //background: rgba(61, 137, 141, 0.95);
    //background: rgba(61, 61, 62, 0.8);
    background: rgba(47, 64, 77, 0.85);
    text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 15px #f68605, 0 0 20px #f68605, 0 0 25px #f68605, 0 0 30px #f68605, 0 0 35px #f68605;
    line-height: normal;
  }

  .details__title-year {
    font-weight: bold;
    text-align: center;
  }

  .details__vote {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.icon {
  position: relative;
  font-size: 75%;
  padding-right: 4px;
  &:before {
    display: block;
    content: '\f005';
    color: #ffffff;
    font-family: 'Font Awesome 5 Free', 'Font Awesome 5 Brands';
    font-weight: 900;
    font-style: normal;
    text-decoration: inherit;
  }
}
